body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  /* or inline-flex */
  border: 1px black solid;
}

.map_item {
  width: 50vw;
  height: 60vh;
  border: 1px black solid;
}

.weather_item {
  width: 50vw;
  height: 60vh;
  border: 1px black solid;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.extra_item {
  width: 33.3vw;
  height: 39vh;
  border: 1px black solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.map_loader {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: auto;
}
/* .weather_container {
  border: 1px solid darkgray;
  border-radius: 4px;
  padding: 20px;
} */

.coords {
  color: gray;
  text-align: right;
  font-size: 0.7em;
  font-style: italic;
}

.currently {
  display: flex;
  align-items: center;
  justify-content: center;
}

.currently-icon {
  padding-right: 12px;
}

.swap {
  font-weight: 600;
  text-align: left;
}

.desc {
  position: relative;
  top: 7px;
  line-height: 1.3em;
  margin-bottom: 15px;
}

.summary {
  font-size: 1.5em;
}
.summary-high-low {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;
}

.high-low-label {
  font-weight: 100;
  /* padding-right: 1.5em; */
  display: inline-block;
  width: 80px;
}
.city_info {
  color: gray;
  text-align: left;
  font-style: italic;
  font-size: 0.7em;
  margin: 12px
}

.city_details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.country {
  font-size: 0.9em;
  font-weight: 600;
  color: darkgray;
}

.city {
  font-size: 1.4em;
  font-weight: 300;
  color: black;
  text-align: center;
}

.street {
  text-align: center;
}

.city_population {}

.time_container {
  display: flex;
  flex-direction: column;
  text-align: center;
  
}

.cannot {
  font-size: 0.7em;
  color: gray;
  padding-top: 8px;
}

.flag {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.27);
}
.news_container {
  height: 100%;
  position: relative;
}

.h4_container {
  text-align: center;
  position: absolute;
  width: calc(100% - 16px);
  padding: 8px;
  
  font-size: 1.2em;
  font-weight: bold;
  color: white;
  background-color: rgba(100, 100, 100, 0.8);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.27);
}

.news_list {
  overflow: scroll;
  padding-top: 40px;
  height: calc(40vh - 50px);
  /* height: 100%; */
  padding-right: 20px;
}
.photos_container {
  display: flex;
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
  justify-content: center;
  /* align-items: stretch; */
  align-content: center;
  overflow: scroll;
}

.no_photos_container {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  /* align-items: stretch; */
  align-content: center;
  overflow: scroll;
}

.photo {
  display: block;
  max-width: 30%;
  max-height: 30%;
  /* width: auto; */
  height: auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.27);
  margin: 4px;
  object-fit: cover;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.photo:hover {
  opacity: 0.7;
}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}

/* Modal Content (image) */
.modal-content {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
}

/* Caption of Modal Image */
#caption {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  height: 150px;
}

/* Add Animation */
.modal-content, #caption {  
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
}

@-webkit-keyframes zoom {
  from {-webkit-transform:scale(0)} 
  to {-webkit-transform:scale(1)}
}

@keyframes zoom {
  from {-webkit-transform:scale(0);transform:scale(0)} 
  to {-webkit-transform:scale(1);transform:scale(1)}
}

/* The Close Button */
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px){
  .modal-content {
    width: 100%;
  }
}


